import React, { useEffect } from "react";
import { useParams } from "react-router-dom";
function Detail() {

    const { mid } = useParams();
    const getMovie = async () => {
        const json = await (
            await fetch(`https://yts.mx/api/v2/movie_details.json?movie_id=${mid}`))
            .json();
    }
    useEffect(() => {
        getMovie();
    }, []);

    return (
        <>
            <h1>hello</h1>
        </>
    );
};

export default Detail;